.empty {
  width: 100%;
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.empty h2 {
  font-size: 1.25rem;
  color: #f9d3b4;
  font-family: var(--font-raleway);
}

.container {
  width: 100%;
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.movie {
  width: 310px;
  height: 460px;
  margin: 1.5rem;

  position: relative;
  border-radius: 12px;
  overflow: hidden;
  border: none;

  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.movie div:nth-of-type(1) {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
  color: #f9d3b4;
}

.movie:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.movie div:nth-of-type(2) {
  width: 100%;
  height: 100%;
}

.movie div:nth-of-type(2) img {
  height: 100%;
  width: 100%;
}

.movie div:nth-of-type(3) {
  z-index: 2;
  background-color: #343739;
  padding: 16px 24px 24px 24px;

  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

.movie div:nth-of-type(3) span {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #f0f0f0;
}

.movie div:nth-of-type(3) h3 {
  margin-top: 5px;
  font-family: "Roboto Slab", serif;
  color: #f9d3b4;
}

.movie:hover div:nth-of-type(2) {
  height: 100%;
  opacity: 0.3;
}

.movie:hover div:nth-of-type(3) {
  background-color: transparent;
}

.movie:hover div:nth-of-type(1) {
  opacity: 1;
}

@media screen and (max-width: 600px) {
  .app {
    padding: 4rem 2rem;
  }

  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: 400px) {
  .app {
    padding: 4rem 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .container {
    margin-top: 2rem;
  }

  .movie {
    width: "100%";
    margin: 1rem;
  }
}


/* movie detail page */

.movie-detail-container {
  display: flex;
  margin: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(255, 255, 255, 0.1);
  overflow: hidden;
  background-color: #1c1c1c;
}

.poster-container {
  flex: 1;
}

.poster {
  width: 100%;
  max-width: 465px; /* Limit the width of the image */
  height: auto;
  margin: 30px;
    border-radius: 8px 
}

.info-container {
  flex: 2;
  padding: 20px;
  color: #ffffff; /* White text color */
}

.title {
  font-family: 'Roboto', sans-serif;
  font-size: 28px;
  margin: 0 0 10px; /* Add some bottom margin */
}

.release-year,
.runtime,
.genre,
.director,
.actors,
.language,
.country,
.awards,
.imdb-rating,
.imdb-votes,
.metascore,
.box-office {
  margin-top: 10px;
  font-size: 18px; /* Increase font size */
}

.plot {
  margin-top: 20px;
  font-size: 20px; /* Increase font size */
}

.ratings {
  margin-top: 20px;
}

.rating {
  margin-bottom: 5px;
  color: #cccccc; /* Light gray color */
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  color: #ffffff; /* White text color */
}

/* star rating css  */

.imdb-rating {
  display: flex;
  align-items: center;
}

.rating {
  margin-right: 5px;
  font-weight: bold;
}

.stars {
  color: #FFD700; /* Gold color for stars */
}

.star {
  font-size: 20px; /* Adjust the size of the stars */
}